<template>
	<div class="Column">
		<Cell
		v-for="item of $store.state.column.column"
		:key="item._id"
		:data="item"
		:isAdd="true"
		api-name="column"
		@add-item="addItem(item)"
		/>
	</div>
</template>

<script>
	import Cell from '../components/Cell.vue'
	import {mapActions} from 'vuex'
	export default{
		name:'Column',
		props:{},
		data(){
			return {
				column:[]
			}
		},
		components:{
			Cell
		},
		activated(){
			this.$store.dispatch('column/'+this.$types.COLUMN)
		},
		updated(){},
		methods:mapActions('shopCar',['addItem'])
	}
</script>

<style>
	.Column{
	    margin-top: .6rem;
	    margin-left: 0.4rem;
	    margin-right: 0.4rem;
	}
</style>
