import {get} from '../service.js'

import * as types from '../types.js'

const state={
	follow:[]
}

let actions={
	[types.FOLLOW]({state,commit},payload){
		get({apiname:'follow',params:{_page:1,_limit:6}}).then(
			res=>commit('FOLLOW',res)
		)
	}
}

let mutations={
	[types.FOLLOW]:(state,payload)=>{
		state.follow=payload
	}
}

export default{
	namespaced:true,
	state,actions,mutations
}