<template>
	<div class="shopCar">
		<h3>shopCar</h3>
		<h3>shopCar</h3>
		<h3>shopCar</h3>
		<h3>shopCar</h3>
		
		<li v-for="(item,index) of shopCar" :key="item._id">
			{{item}}/{{index}}
			<button @click="changeItem({_id:item._id,num:1})">+</button>
			<button @click="changeItem({_id:item._id,num:-1})">-</button>
			<button @click="removeItem({_id:item._id})">x</button>
		</li>
		<button @click="clearShopCar" v-show="shopCar.length>0">清空购物车</button>
	</div>
</template>

<script>
	import {mapActions,mapState} from 'vuex'
	export default{
		name:'shopCar',
		parops:{},
		methods:mapActions('shopCar',['changeItem','clearShopCar','removeItem']),
		computed:mapState('shopCar',['shopCar'])
	}
</script>

