const state={
	shopCar:[]
}

const actions={
	addItem({commit,state},payload){
		console.log(payload)
		let arr=[state.shopCar];
		let find=false;
		arr.forEach((item,index)=>{
			if(item._id==payload._id){
				item.num++;//数量递增
				find=true
			}
		})
		
		if(!find){
			payload.num=1;
			arr.push(payload)
		}
		
		commit('ADD_ITEM',arr)
	},
	changeItem({commit,state},payload){
		state.shopCar.forEach((item,index)=>{
			if(item._id==payload._id){
				item.num+=payload.num
			}
		})
		
		commit('CHANGE_ITEM',[...state.shopCar])
	},
	removeItem({commit,state},payload){
		state.shopCar.forEach((item,index)=>{
			if(item._id==payload._id){
				state.shopCar.splice(index,1)
			}
		})
		
		commit('REMOVE_ITEM',[...state.shopCar])
	},
	clearShopCar:({commit,state})=>commit('CLEAR_SHOPCAR',[])
}

const mutations={
	ADD_ITEM:(state,payload)=>{
		state.shopCar=payload
		console.log(state.shopCar)
	},
	CHANGE_ITEM:(state,payload)=>{
		state.shopCar=payload
	},
	REMOVE_ITEM:(state,payload)=>{
		state.shopCar=payload
	},
	CLEAR_SHOPCAR:(state,payload)=>{
		state.shopCar=payload
	}
}

export default{
  namespaced:true,//命名空间的模块。模块内部直接调用，外部需要命名空间(见组件)
  state,actions,mutations
}