import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

import state from '../store/state.js';
import getters from '../store/getter.js';
import mutations from '../store/mutation.js';

import banner from '../store/modules/banner.js'
import home from '../store/modules/home.js'
import follow from '../store/modules/follow.js'
import user from '../store/modules/user.js'
import column from '../store/modules/column.js'
import detail from '../store/modules/detail.js'
import shopCar from '../store/modules/shopCar.js'

export default new Vuex.Store({
	state,getters,mutations,
	modules:{
		banner,home,follow,user,column,detail,shopCar
	}
})